import React from 'react';
import './filter.scss';

class Filter extends React.Component {
    constructor() {
        super();
        this.state = {};
    }

    render() {
        return (
            <div className="filters-wrapper">
                <div className="filters-component">
                    <p>Type</p>
                </div>
                <div className="clear-filters purple">Clear All Filters</div>
            </div>
        );
    }
}

export default Filter;
